<template>
  <div>
    <!-- <button class="but" @click="$router.push('/login')">退出</button> -->
    <div class="title">
      <!-- 头像 -->
      <div class="pic">
        <img :src="img" alt />
      </div>

      <!-- 昵称 -->
      <h3>{{username}}</h3>
    </div>
  </div>
</template>

<script>
import tou from "../../assets/img/1.jpg";
export default {
  mounted(){
   let user =  JSON.parse(localStorage.getItem("user"))
   console.log(user)
   this.username = user.nickname
  //  console.log(user)
  },
  data() {
    return {
      img: tou,
      username:""
    };
  }
};
</script>
<style scoped lang="stylus">
@import '../../stylus/index.styl';
.title{
  background $primary
  height 2.4rem
  width 100vw
  position relative
  h3{
    color #000
    position absolute
    left 50%
    transform translate(-50%,0)
    bottom -1.3rem
    font-size .3rem
  }
  
}

.pic {
  border-radius: 100%;
  height: 2rem;
  width: 2rem;
  overflow: hidden;
  margin: auto;
  position  absolute
  left 50%
  top 50%
  transform translate(-50%,0)
  z-index 1
  img {
    height: 100%;
    width: 100%;
  }
}
button{
  position fixed
  top 0
  right 0
  line-height $h5
  font-size: $h2;
  padding 0
  margin-right .13rem
  z-index 2
}
</style>


